@import "../../styles/utils.scss";
@include mobile {
  .review-modal {
    .modal {
      height: 75% !important;
      width: 80vw !important;
      border-radius: 10px;
      @include boxShadox(0px, 0px, 11px, 3px, $white-grey);
      .form-container {
        justify-content: space-between;
        width: 100%;
        height: 100%;
        img {
          width: 40%;
          height: 40%;
          margin-top: 30px;
          margin-bottom: 0;
          border: 1px solid #e3e3e3;
          padding: 5px;
        }
        form {
          width: 80%;
          height: 50%;
          margin: 10px;
          @include flex(column, center, center);
          textarea {
            width: 100%;
            height: 100%;
            resize: none;
            font-size: 0.8rem;
            padding: 5px;
            border-radius: 5px;
          }
          input[type="submit"] {
            @include base-button($blue, 100%);
          }
          .cancel {
            @include base-button($red, 100%);
            margin: 2px;
          }
        }
      }
    }
  }
}
